@import "mixin/mixins";
@import "mixin/fonts";
@import "mixin/variables";
@import "mixin/normalize";



@import "_fixed_header.sass";

@import "_header.sass";

.ancor_block {
	position: fixed;
	z-index: 50;
	left: 0;
	bottom: 30px;
	width: 100%;
	.container {
		text-align: right;
		.ancor {
			width: 80px;
			height: 80px;
			background: $white;
			opacity: 0.8;
			border-radius: 50%;
			position: relative;
			display: inline-block;
			border: 2px solid #8be4f9;
			// @include box-shadow(0 0 0 6px #8be4f9)
			img {
				position: absolute;
				top: 50%;
				left: 50%;
				@include translate(-50%, -50%); } } } }

.aside {
	position: relative; }
.aside_button {
	display: none; }
.aside_container {
	width: 100%;
	// height: 100px
	position: absolute;
	top: -30px;
	left: 3px;
	z-index: 15;
	background: #e8fcff url('../img/aside_panel_shadows.png') no-repeat 0% 0%;
	background-size: cover;
	border: 1px solid #b5e4fc;
	padding-top: 37px;
	&:after {
		content: '';
		position: absolute;
		width: 100%;
		height: 13px;
		left: -1px;
		bottom: -16px;
		background: #a8edfc;
		border: 1px solid #a8edfc;
		border-top: 1px solid $white;
		border-radius: 0 0 10px 10px; } }
.aside_action_baner {
	&_h {
		background: #e90000;
		font-size: 27px;
		font-weight: 700;
		line-height: 29px;
		color: $white;
		text-align: center;
		padding: 3px 5px;
		width: 99%;
		margin: auto; }
	&_img {
		height: 200px;
		width: 100%;
		position: relative;
		overflow: hidden;
		display: block;
		@include cursor;
		img {
			position: absolute;
			top: 50%;
			left: 50%;
			@include translate(-50%, -50%);
			max-width: 100%;
			max-height: 100%; } }
	&_link {
		background: #e90000;
		color: $white;
		font-size: 14px;
		font-weight: 700;
		display: block;
		width: 99%;
		margin: auto;
		text-align: center;
		padding: 4px 5px;
		margin-bottom: 2px; } }

.aside_category {
	ul {
		list-style-type: none;
		padding: 0;
		margin: 0; }
	&_h {
		font-size: 24px;
		font-weight: 700;
		font-style: italic;
		color: $white;
		background: $blue;
		padding: 9px 7px;
		width: 99%;
		margin: auto;
		&:before {
			content: url('../img/category_list_before.png');
			margin-right: 5px; } }
	&_main_list {
		&_name {
			font-size: 20px;
			font-weight: 700;
			color: $blue;
			position: relative;
			padding: 12px 25px;
			display: block;
			border-bottom: 1px solid #b5e4fc;
			@include cursor;
			@include transition(0.2s);
			span {
				position: relative;
				z-index: 10; }
			&:before {
				content: '';
				width: 0;
				height: 0;
				border-bottom: 14px solid $blue;
				border-left: 14px solid transparent;
				position: absolute;
				bottom: 0px;
				right: 0;
				z-index: 10;
				@include transition(0.2s); }
			&:after {
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				z-index: 5;
				@include linear-gradient(to top, #cef8ff 0%, #7fe0f8 100%);
				opacity: 0;
				@include transition(0.2s); }
			&:hover {
				color: #123236;
				&:before {
					border-bottom: 14px solid #6ecbe2; }
				&:after {
					opacity: 1; } } } }
	&_child_list {
		display: none;
		li {
			position: relative;
			border-bottom: 1px solid #b5e4fc;
			&:after {
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				z-index: 5;
				@include linear-gradient(to top, #cef8ff 0%, #7fe0f8 100%);
				opacity: 0;
				@include transition(0.2s); }
			&:hover {
				&:after {
					opacity: 1; } } }
		&_name {
			@include cursor;
			color: $blue;
			display: block;
			font-weight: 400;
			font-size: 20px;
			padding: 12px 25px;
			padding-left: 50px;
			position: relative;
			z-index: 10;
			@include transition(0.2s);
			background: #eef8fb;
			span {
				position: relative;
				z-index: 10; }
			&:hover {
				color: #123236; }
			&:after {
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				z-index: 5;
				@include linear-gradient(to top, #cef8ff 0%, #7fe0f8 100%);
				opacity: 0;
				@include transition(0.2s); }
			&:hover {
				&:after {
					opacity: 1; } } } } }

.aside_offer {
	&_h {
		background: $blue;
		padding: 12px 25px;
		margin-bottom: 6px;
		font-size: 24px;
		color: $white;
		font-weight: 700;
		font-style: italic;
		position: relative;
		&:before {
			content: url('../img/offer_star_icon.png');
			position: absolute;
			left: 5px; } }
	&_block {
		margin-top: 17px;
		margin-bottom: 12px;
		&_h {
			display: block;
			height: 40px;
			text-align: center;
			color: $blue;
			font-size: 20px;
			line-height: 20px;
			font-weight: 700;
			padding: 0 10px;
			margin-bottom: 7px;
			overflow: hidden;
			&:hover {
				text-decoration: underline; } }
		&_img {
			border: 1px solid $blue;
			overflow: hidden;
			height: 206px;
			position: relative;
			background: $white;
			@include transition(0.2s);
			&:hover {
				opacity: 0.7; }
			img {
				max-width: 100%;
				max-height: 100%;
				position: absolute;
				top: 50%;
				left: 50%;
				@include translate(-50%, -50%); }
			&_wrapper {
				display: block;
				margin-bottom: 10px;
				position: relative;
				padding-bottom: 1px;
				border-bottom: 2px solid #fa0101;
				max-width: 213px;
				margin: auto;
				&:before,
				&:after {
					content: '';
					width: 2px;
					height: 99%;
					position: absolute;
					top: 0;
					background: #fa0101; }
				&:before {
					left: -3px; }
				&:after {
					right: -3px; } } }
		&_rating {
			font-size: 24px;
			color: #d3d3d3;
			height: 29px;
			& i.active {
				color: #fcd521; } }
		&_price {
			color: $white;
			position: relative;
			font-size: 30px;
			text-align: center;
			width: 90%;
			// line-height: 33px
			margin: auto;
			margin-top: 17px;
			& span {
				line-height: 26px; }
			& i {
				font-size: 14px; }
			& > div {
				position: relative;
				z-index: 10; }
			&:before {
				// content: ''
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background: $blue;
				z-index: 5;
				@include transform(skew(-0.05turn, 0deg)); }

			width: 80%;
			font-size: 0;
			& > * {
				width: 50%;
				display: inline-block;
				vertical-align: top;
				font-size: 16px; }
			.old_price {
				position: relative;
				color: $blue;
				text-align: left;
				padding-top: 9px;
				.price {
					position: relative;
					z-index: 10;
					text-align: center;
					// padding-left: 20px
					font-size: 20px;
					text-decoration: line-through;
					&_caption {
						position: relative;
						z-index: 10;
						font-size: 15px; } }
				&:before {
					content: '';
					background: $white;
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					z-index: 5;
					@include transform(skew(-0.05turn, 0deg)); } }
			.new_price {
				position: relative;
				color: $white;
				.price {
					position: relative;
					z-index: 10;
					text-align: center;
					// padding-left: 20px
					font-size: 30px;
					line-height: 35px;
					&_caption {
						position: relative;
						z-index: 10;
						font-size: 15px;
						text-transform: uppercase;
						text-align: right;
						padding-right: 10px; } }
				&:before {
					content: '';
					background: $blue;
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					z-index: 5;
					@include transform(skew(-0.05turn, 0deg)); } } }



		&_basket {
			@include cursor;
			border: 1px solid #59cad7;
			@include linear-gradient(to top, #cef8ff 0%, #7fe0f8 100%);
			color: #555555;
			font-size: 15px;
			font-weight: 700;
			text-shadow: {}
			display: block;
			margin: auto;
			text-align: center;
			max-width: 91px;
			padding: 3px 5px;
			margin-top: 10px;
			border-radius: 3px; } } }



.main_slider {
	position: relative;
	margin-top: 5px;
	.slick-arrow {
		background: none;
		border: none;
		font-size: 0;
		position: absolute;
		top: 50%;
		@include translate(0, -50%);
		z-index: 10;
		outline: none;
		@include cursor;
		&:hover {
			@include transform(translate(0, -50%) rotate(180deg));
			&:after {
				content: url('../img/arrow_hover.png'); } }

		&:after {
			content: url('../img/arrow.png'); }
		&.slick-prev {
			left: 20px; }
		&.slick-next {
			@include transform(translate(0, -50%) rotate(180deg));
			right: 20px;
			&:hover {
				@include transform(translate(0, -50%) rotate(0deg));
				&:after {} } } }
	.slick-dots {
		list-style-type: none;
		position: absolute;
		bottom: 10px;
		left: 50%;
		@include translate(-50%, 0);
		padding: 0;
		margin: 0;

		li {
			display: inline-block;
			margin-right: 8px;
			button {
				@include cursor;
				font-size: 0;
				width: 19px;
				height: 19px;
				border: 2px solid #62defb;
				background: $white; }
			&.slick-active {
				button {
					border: 2px solid $white;
					background: $blue; } } } }
	&_block {
		border-radius: 10px;
		img {
			width: 100%;
			height: 100%;
			object-fit: cover; } } }

