.header {
	background: url(../img/header_bg.jpg) no-repeat 50% 50%;
	background-size: cover;
	.container {
		font-size: 0;
		& > * {
			// font-size: 14px
			display: inline-block;
 } }			// vertical-align: middle
	&_logo_block {
		display: inline-block;
		vertical-align: top; }
	.header_logo_content {
		display: inline-block;
		vertical-align: top;
		& > * {
			// font-size: 14px
			display: inline-block; } }

	&_logo {
		display: block;
		padding-left: 8px;
		padding-right: 59px;
		margin-right: 28px;
		@include linear-gradient(to top, #7fe0f8 0%, #cef8ff 100%);
		position: relative;
		&:before,
		&:after {
			content: '';
			position: absolute;
			top: 50%;
			@include translate(0, -50%);
			height: 15px;
			width: 7px; }
		&:before {
			right: 2px;
			background: #53bfd9;
			border-radius: 100% 0 0 100% / 50% 0 0 50%; }
		&:after {
			right: -8px;
			background: #f0fdff;
			border-radius: 0 100% 100% 0 / 0 50% 50% 0; }
		& > * {
			display: inline-block;
			vertical-align: middle; }
		&_shadow {
			position: absolute;
			top: 0;
			left: 0;
			display: block;
			width: 100%;
			height: 100%;
			&:before {
				content: url('../img/logo_shadow_before.png');
				position: absolute;
				top: 0;
				left: -35px; }
			&:after {
				content: url('../img/logo_shadow_after.png');
				position: absolute;
				top: 0;
				right: -35px; } }
		&_bottom {
			display: block;
			width: 100%;
			// height: 100%
			height: 42px;
			position: absolute;
			bottom: -40px;
			left: 0;
			background: #7fe0f8; }

		&_text {
			margin-left: -30px;
			font-family: 'Myriad Pro';
			font-style: italic;
			font-weight: bold;
			span {
				display: block;
				color: $blue;
				font-size: 36px;
				line-height: 38px;
				letter-spacing: -1.44px;
				&:last-child {
					font-size: 60px;
					letter-spacing: -2.4px; } } } }
	&_caption {
		color: $blue;
		font-size: 26px;
		letter-spacing: 0.39px;
		vertical-align: middle;
		margin-right: 30px;
		margin-top: 5px; }
	&_info {
		font-family: Arial;
		font-size: 14px;
		vertical-align: top;
		margin-right: 20px;
		margin-top: 7px; }
	&_basket,
	&_sign {
		display: inline-block;
		vertical-align: middle;
		& > * {
			display: inline-block;
			vertical-align: middle; } }
	&_sign {
		margin-right: 20px;
		a {
			margin-left: 6px;
			margin-right: 6px;
			position: relative;
			color: #123236;
			@include transition(0.1s);
			@include cursor;
			&:hover {
				color: $link_blue;
				&:before {
					background: $link_blue; } }
			&:before {
				content: '';
				width: 100%;
				height: 1px;
				background: #123236;
				position: absolute;
				left: 0;
				bottom: -1px; } }
		.header_sign_up {
			&:after {
				content: '';
				position: absolute;
				right: -9px;
				bottom: 0;
				background: #a9dee9;
				width: 2px;
				height: 15px; } } }
	&_basket {
		display: inline-block;
		vertical-align: middle;
		color: #999999;

		&_img {
			display: inline-block;
			vertical-align: middle;
			img {
				margin-top: 3px; }
			&.complete {
				position: relative;
				&:after {
					content: '';
					position: absolute;
					top: 1px;
					left: 5px;
					width: 6px;
					height: 6px;
					background: url('../img/basket_complete_icon.png') no-repeat 50% 50%;
					border-radius: 50%;
					border: 1px solid #ffffff;
					@include box-shadow(0 0 0 2px rgba(0, 198, 255, 0.39)); } } }
		&_quantity {
			color: $link_blue;
			position: relative;
			&:after {
				content: '';
				width: 100%;
				height: 1px;
				background: $link_blue;
				position: absolute;
				left: 0;
				bottom: -1px; } }
		&_price {
			color: $blue;
			font-size: 20px; } }

	&_contacts {
		vertical-align: middle;
		font-size: 14px;
		position: relative;
		margin-top: 12px;

		&:before,
		&:after {
			position: absolute;
			top: 50%;
			@include translate(0, -50%); }
		&:before {
			content: url('../img/callback_before.png');
			left: -20px; }
		&:after {
			content: url('../img/callback_after.png');
			right: 5px; }
		&_call_back {
			display: inline-block;
			vertical-align: middle;
			.phone_img {
				display: inline-block;
				width: 56px;
				height: 56px;
				background: $white url('../img/phone_icon.png') no-repeat 50% 50%;
				padding: 5px;
				border-radius: 50%;
				position: relative;
				&:after {
					content: '';
					position: absolute;
					top: -8px;
					left: -8px;
					width: 68px;
					height: 68px;
					border: 2px solid #a3e9f9;
					border-radius: 50%; } }
			span {
				color: $blue;
				font-size: 14px;
				font-weight: 700;
				line-height: 13px;
				text-transform: uppercase;
				max-width: 60px;
				display: block;
				text-align: center;
				margin-top: 10px; } }
		&_number {
			display: inline-block;
			vertical-align: middle;
			margin-left: 20px;
			&_item {
				display: block;
				font-size: 30px;
				font-weight: 700;
				.pale_blue {
					color: #47949d; }
				.dark_blue {
					color: #20666e; } } } } }

.header_menu {
	@include linear-gradient(to top, #7fe0f8 0%, #cef8ff 100%);
	.container {
		text-align: right; }
	// &_block
	// 	float: left
	// 	max-width: 338px
	// 	width: 23.97%
	// 	max-height: 42px
	// 	background: #7fe0f8
	&_button {
		display: none; }
	&_list {
		max-width: 1050px;
		margin-left: auto;
		padding: 0 30px;
		list-style-type: none;
		text-align: justify;
		font-size: 0;
		&:after {
			content: '';
			display: inline-block;
			width: 100%; }
		&_item {
			font-size: 18px;
			width: 14.28%;
			display: inline-block;
			vertical-align: middle;
			text-transform: uppercase;
			position: relative;
			&:last-child {
				&:after {
					content: '';
					position: absolute;
					top: 0;
					right: 0;
					width: 1px;
					height: 80%;
					@include linear-gradient(to top, #22AAC8 0%, #CCF7FE 100%); } }
			&:before {
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				width: 1px;
				height: 80%;
				@include linear-gradient(to top, #22AAC8 0%, #CCF7FE 100%); }
			&:hover {
				& + .header_menu_list_item:before {
					display: none; }
				&:before,
				&:after {
					display: none; } }
			a {
				padding: 10px;
				display: block;
				text-align: center;
				position: relative;
				cursor: url('../img/cursor_icon.png') 10 3, auto;
				@include transition(0.2s);
				span {
					position: relative;
					z-index: 5; }
				&:after {
					content: '';
					width: 100%;
					height: 100%;
					position: absolute;
					top: 0;
					left: 0;
					@include transition(0.2s);
					@include linear-gradient(to top, #cef8ff 0%, #7fe0f8 100%);
					opacity: 0;
					z-index: 0; }
				&:hover {
					color: $link_blue;
					&:after {
						opacity: 1; } } } } } }
