.basket_name {
	margin-bottom: 55px;
	h1 {
		text-align: center;
		font-size: 39px;
		color: $blue;
		margin-top: 35px;
		margin-bottom: 0px; }
	&_img {
		overflow: hidden;
		text-align: center;
		margin-bottom: 25px;
		position: relative;
		height: 47px;
		img {
			position: absolute;
			left: 50%;
			@include translate(-50%, 0); } } }

.order {

	&_h {
		color: $blue;
		font-size: 18px;
		font-weight: 700;
		text-transform: uppercase;
		border-bottom: 1px solid #d8d8d8;
		padding-bottom: 8px; }
	&_block {
		background: #f3fcff;
		margin-top: 1px;
		padding: 10px 12px;
		&_h {
			background: #a5ebfb;
			color: $blue;
			font-size: 0;
			span {
				font-size: 14px;
				display: inline-block;
				vertical-align: middle;
				padding-left: 7px;
				&:nth-child(1) {
					width: 6.34%; }
				&:nth-child(2) {
					width: 32.94%; }
				&:nth-child(3) {
					width: 13.16%;
					padding-left: 15px; }
				&:nth-child(4) {
					width: 12.48%;
					padding-left: 25px; }
				&:nth-child(5) {
					width: 12.48%; }
				&:nth-child(6) {
					width: 17.25%;
					text-align: center; } } }

		&_content {
			padding: 10px 0;
			background: $white;
			font-size: 0;
			border-bottom: 1px solid #dadada;
			margin-bottom: 6px;
			position: relative;
			& > * {
				font-size: 14px;
				display: inline-block;
				vertical-align: top; } } }

	&_article {
		color: $blue;
		font-size: 15px;
		width: 6.34%;
		padding-left: 7px; }

	&_name {
		width: 32.94%;
		&_link {
			max-height: 105px;
			overflow: hidden;
			padding-left: 5px;
			padding-right: 5px;
			display: inline-block;
			vertical-align: top;
			max-width: 210px;
			color: $link_blue;
			text-align: justify;
			&:after {
				width: 100%;
				display: inline-block; }
			&:hover {
				text-decoration: underline; } }
		&_img {
			display: inline-block;
			vertical-align: top;
			max-width: 120px;
			border: 1px solid #b6cdcf;
			@include cursor;
			img {
				width: 95%;
				height: 95%;
				object-fit: cover; } } }

	&_price {
		width: 13.16%;
		color: $blue;
		font-size: 25px;
		background: #f3fcff;
		padding: 1px 0;
		position: relative;
		margin-left: -4px;
		padding-left: 10px;
		span {
			position: relative;
			z-index: 10; }
		.fa {
			font-size: 14px;
			position: relative;
			z-index: 10; }
		&:after {
			content: '';
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			right: -5px;
			background: #f3fcff;
			@include transform(skew(-0.04turn, 0deg)); } }


	&_unit {
		width: 13.48%;
		padding-top: 20px;
		&_h {
			font-size: 14px;
			color: $blue;
			line-height: 20px;
			text-align: center; }
		.counter_block {
			position: relative;
			width: 127px;
			margin-left: auto;
			text-align: center;
			input {
				text-align: center;
				border: 2px solid #3c555b;
				background: $white;
				border-radius: 10px;
				font-size: 18px;
				width: 53px;
				height: 32px; }
			.count_btn {
				span {
					position: absolute;
					top: -2px;
					background: #6bd4f7;
					border-radius: 5px;
					width: 34px;
					height: 34px;
					line-height: 34px;
					font-size: 18px;
					color: $white;
					text-align: center;
					@include cursor;
					@include transition(0.2s);
					padding-top: 1px;
					&:hover {
						background: $blue; } }
				.plus {
					right: 0px; }
				.minus {
					left: 0px; } } } }


	&_quantity {
		width: 13.48%;
		.counter_block {
			position: relative;
			width: 127px;
			margin-left: auto;
			text-align: center;
			input {
				text-align: center;
				border: 2px solid #3c555b;
				background: $white;
				border-radius: 10px;
				font-size: 18px;
				width: 53px;
				height: 32px; }
			.count_btn {
				span {
					position: absolute;
					top: -1px;
					background: #6bd4f7;
					border-radius: 5px;
					width: 34px;
					height: 34px;
					line-height: 34px;
					font-size: 18px;
					color: $white;
					text-align: center;
					@include cursor;
					@include transition(0.2s);
					&:hover {
						background: $blue; } }
				.plus {
					right: 0px; }
				.minus {
					left: 0px; } } } }

	&_cost {
		width: 17.25%;

		&_price {
			color: $white;
			position: relative;
			font-size: 30px;
			text-align: center;
			width: 90%;
			line-height: 33px;
			margin: auto;
			& span {
				line-height: 26px; }
			& i {
				font-size: 14px; }
			& > div {
				position: relative;
				z-index: 10; }
			&:before {
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background: $blue;
				z-index: 5;
				@include transform(skew(-0.05turn, 0deg)); } } }

	&_delete {
		width: 34px;
		height: 34px;
		border: 3px solid #f3fcff;
		background: $green;
		border-radius: 50%;
		position: relative;
		@include transition(0.2s);
		@include cursor;
		position: absolute;
		right: 7px;
		top: 7px;
		&:hover {
			background: #6bd4f7; }
		img {
			position: absolute;
			top: 5px;
			left: 5px; } }

	&_total {
		margin-top: 15px;
		padding-right: 37px;
		position: relative;
		&:after {
			content: "";
			display: table;
			clear: both; }
		&:before {
			content: '';
			width: 100%;
			height: 1px;
			background: #dadada;
			position: absolute;
			top: 19px;
			left: 0;
			z-index: -1; }
		&_price {
			float: right;
			text-align: center;
			&_caption {
				display: inline-block;
				vertical-align: middle;
				color: $blue;
				font-size: 30px;
				// margin-right: 50px
				min-width: 220px;
				text-align: center;
				position: relative;
				background: $white;
				&:before,
				&:after {
					content: '';
					width: 13px;
					height: 100%;
					position: absolute;
					top: 0;
					@include transform(skew(-0.05turn, 0deg)); }
				&:before {
					background: #ebebeb;
					left: 0px; }
				&:after {
					background: #a5ebfb;
					left: 23px; } }

			&_number {
				display: inline-block;
				vertical-align: middle;
				position: relative;
				font-size: 30px;
				line-height: 32px;
				color: $white;
				padding-left: 25px;
				padding-right: 25px;
				div {
					position: relative;
					z-index: 10;
					i {
						font-size: 15px; } }
				&:after {
					content: '';
					width: 120%;
					height: 100%;
					position: absolute;
					left: -10%;
					top: 0;
					background: $green;
					@include transform(skew(-0.05turn, 0deg)); } } } } }

.phone {
	margin-top: 10px;
	font-size: 0;
	.add_new_form {
		font-size: 15px;
		color: $link_blue;
		text-decoration: underline;
		display: block;
		text-align: center;
		margin-top: 15px;
		@include cursor; }
	.form {
		display: inline-block;
		vertical-align: middle;
		width: 48.85%;
		padding: 0 40px;
		span {
			display: block;
			font-size: 15px;
			text-align: center;
			margin-bottom: 7px; }
		input {
			border-radius: 10px;
			border: 2px solid #3c555b;
			color: $blue;
			font-size: 20px;
			display: block;
			width: 100%;
			padding: 18px;
			margin-bottom: 10px;
			outline: none;
			text-align: center;
			font-weight: 700; } }
	&_caption {
		margin-top: 50px;
		display: inline-block;
		vertical-align: top;
		width: 51.15%;
		font-size: 15px;
		color: $blue; } }



.delivery_method {
	margin-top: 50px;
	&_h {
		font-size: 18px;
		font-weight: 700;
		color: $blue;
		text-transform: uppercase;
		padding-bottom: 8px;
		border-bottom: 1px solid #d8d8d8; }
	form {
		background: #f3fcff;
		padding: 10px;
		div {
			padding-left: 85px;
			position: relative;
			&:before {
				position: absolute;
				top: 50%;
				left: 20px;
				@include translate(0, -50%); }
			&:nth-child(1) {
				&:before {
					content: url('../img/radio_icon_1.png'); } }
			&:nth-child(2) {
				&:before {
					content: url('../img/radio_icon_2.png');
					left: 24px; } }
			&:nth-child(3) {
				&:before {
					content: url('../img/radio_icon_3.png'); } }
			input[type=radio]:checked + label {
				background: #bef4ff;
				&:before {
					opacity: 1; } }
			input {
				display: inline-block;
				vertical-align: middle;
				@include cursor;
				position: absolute;
				left: 80px;
				top: 50%;
				@include translate(0, -50%); }
			label {
				display: block;
				margin-left: 30px;
				background: $white;
				padding: 13px 20px;
				// display: inline-block
				vertical-align: middle;
				margin-bottom: 2px;
				font-size: 17px;
				color: $blue;
				font-weight: 700;
				@include cursor;
				// margin-left: 5px
				position: relative;
				@include transition(0.2s);
				&:hover {
					background: #bef4ff;
					&:before {
						opacity: 1; } }
				&:before {
					content: '';
					width: 5px;
					height: 100%;
					position: absolute;
					left: -5px;
					top: 0;
					@include transition(0.2s);
					background: $green;
					opacity: 0; }

				span {
					display: block;
					&:last-child {
						margin-top: 18px;
						font-weight: 400; } } } } } }


.payment_method {
	margin-top: 30px;
	&_h {
		font-size: 18px;
		font-weight: 700;
		color: $blue;
		text-transform: uppercase;
		padding-bottom: 8px;
		border-bottom: 1px solid #d8d8d8; }
	form {
		background: #f3fcff;
		padding: 10px;
		div {
			padding-left: 85px;
			position: relative;
			&:before {
				position: absolute;
				top: 50%;
				left: 30px;
				@include translate(0, -50%); }
			&:nth-child(1) {
				&:before {
					content: url('../img/radio_icon_4.png'); } }
			&:nth-child(2) {
				&:before {
					content: url('../img/radio_icon_5.png'); } }
			&:nth-child(3) {
				&:before {
					left: 21px;
					content: url('../img/radio_icon_6.png'); } }
			input[type=radio]:checked + label {
				background: #bef4ff;
				&:before {
					opacity: 1; } }
			input {
				display: inline-block;
				vertical-align: middle;
				@include cursor;
				position: absolute;
				left: 80px;
				top: 50%;
				@include translate(0, -50%); }
			label {
				display: block;
				margin-left: 30px;
				background: $white;
				padding: 13px 20px;
				// display: inline-block
				vertical-align: middle;
				margin-bottom: 2px;
				font-size: 17px;
