					width: 5px;
					height: 100%;
					position: absolute;
					left: -5px;
					top: 0;
					@include transition(0.2s);
					background: $green;
					opacity: 0; }

				span {
					display: block;
					&:last-child {
						margin-top: 18px;
						font-weight: 400; } } } } } }


.total_cost {
	.order_total_price_caption {
		padding-left: 50px;
		padding-right: 40px; } }

.total_weight {
	.order_total_price_caption {
		padding-left: 25px; } }


.buyer_info {
	&_h {
		font-size: 18px;
		font-weight: 700;
		text-transform: uppercase;
		color: $blue;
		margin-top: 30px;
		margin-bottom: 15px;
		border-bottom: 1px solid #d8d8d8; }
	&_form {
		font-size: 0;
		div {
			font-size: 14px;
			display: inline-block;
			vertical-align: middle;
			&:nth-child(2n-1) {
				width: 48.85%; }
			&:nth-child(2n) {
				width: 51.15%; }
			&:not(.buyer_info_form_caption) {
				padding-left: 40px;
				padding-right: 35px;
				margin-bottom: 20px; }
			&:last-child {
				width: auto; }
			label {
				font-weight: 700;
				font-size: 16px;
				color: $blue; }
			input,
			textarea {
				display: block;
				border-radius: 10px;
				border: 2px solid #3c555b;
				text-align: center;
				padding: 20px;
				width: 100%; }
			textarea {
				resize: vertical;
				min-height: 60px;
				height: 60px; } }
		&_caption {
			span {
				display: block; }
			a {
				color: $link_blue;
				text-decoration: underline; } }
		.send_form {
			width: 100%;
			display: block;
			margin-top: 100px;
			position: relative;
			.button_block {
				span {
					position: relative;
					left: 0;
					top: 0;
					margin: 0;
					padding: 0;
					font-size: 30px; } }
			span {
				position: absolute;
				top: 0;
				left: 40px;
				margin-right: 1.3%;
				max-width: 310px;
				color: $blue;
				font-size: 15px;
				line-height: 22px; }
			.button_block {
				// width: 64.48%
				margin-left: 320px;
				padding-left: 0 !important;
				padding-right: 0 !important; }
			button {
				width: 100%;
				border-radius: 5px;
				@include linear-gradient(to top, #cef8ff 0%, #7fe0f8 100%);
				border: 1px solid #59cad7;
				color: #555555;
				font-weight: 700;
				font-size: 30px;
				padding: 7px;
				text-shadow: 1px 1px 0 rgba(255, 255, 255, 0.75);
				@include cursor; } } }

	.success_politic {
		color: $blue;
		font-size: 15px;
		float: right;
		padding-left: 0 !important;
		padding-right: 0 !important;
		margin-top: 18px;
		a {
			text-decoration: underline;
			color: $link_blue; } } }

.count_btn {
	span {
		padding-top: 1px; } }
