.fixed_header {
	background: url(../img/header_bg.jpg) no-repeat 50% 50%;
	background-size: cover;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 50;
	width: 100%;
	.container {
		position: relative; }
	.header_logo_shadow {
		&:before,
		&:after {
			top: auto;
			bottom: 0; } }
	.aside_container {
		&:after {
			display: none; } }
	.header_logo_block {
		width: 343px;
		height: 100%;
		position: absolute;
		top: 0;
		left: 8px;
		&:hover {
			.aside_container {
				display: block; } } }
	.aside_category_h {
		@include cursor; }
	.header_logo {
		margin: 0;
		padding: 0;
		position: relative;
		height: 100%;
		@include cursor;
		&:before,
		&:after {
			display: none; }
		&_name {
			width: 100%;
			position: absolute;
			top: 50%;
			left: 50%;
			@include translate(-50%, -50%);
			display: block;
			text-align: center;
			font-size: 24px;
			font-weight: 700;
			font-style: italic;
			color: $blue; } }

	.aside_container {
		z-index: 20;
		top: auto;
		left: 0;
		padding-top: 0;
		display: none; }

	.aside_category {
		position: relative;
		background: $blue;
		&:after {
			content: '';
			width: 100%;
			height: 100%;
			position: absolute;
			top: 0;
			left: 0;
			@include linear-gradient(to top, #cef8ff 0%, #7fe0f8 100%);
			@include transition(0.2s);
			opacity: 0; }
		&:hover {
			&:after {
				opacity: 1; } }
		&_h {
			position: relative;
			z-index: 10;
			background: none;
 }			// &:hover
		&:hover {
			.aside_category_main_list {
				display: block;
				.aside_category_child_list {
					display: none; } } }


		.aside_category_main_list {
			position: absolute;
			top: 0;
			right: -230px;
			background: #e8fcff;
			z-index: 25;
			display: none;
			// .aside_category_main_list_name
			li {
				&:hover {
					.aside_category_child_list {
						display: block; } } } }


		.aside_category_child_list {
			display: block;
			position: absolute;
			top: 0;
			right: -237px; }


		.aside_category_main_list_name {
			&:before {
				display: none; } } }

	.header_logo_content {
		font-size: 0;
		margin-left: 343px;
		padding-left: 35px; }
	.header_contacts {
		margin-top: 0;
		display: inline-block;
		vertical-align: middle;
		&:before,
		&:after {
			display: none; } }
	.header_contacts_call_back {
		margin-top: 6px;
		margin-bottom: 10px;
		.phone_img {
			width: 40px;
			height: 40px;
			background-size: 50%;
			display: inline-block;
			vertical-align: middle;
			&:after {
				width: 45px;
				height: 45px;
				top: -4px;
				left: -4px; } }
		span {
			display: inline-block;
			vertical-align: middle;
			width: 65px;
			margin-left: 10px; } }
	.header_contacts_number {
		// margin-top: 10px
		margin-left: 0;
		margin-right: 35px; }

	.header_contacts_number_item {
		display: inline-block;
		position: relative;
		margin-left: 25px;
		&:after {
			content: url(../img/callback_before.png);
			position: absolute;
			top: -5px;
			left: -16px; } }

	.header_info {
		display: inline-block;
		vertical-align: middle;
		margin-top: 0;
		margin-right: 0; }
	&_order_btn {
		color: #555555;
		text-shadow: 1px 1px 0 rgba(255, 255, 255, 0.75);
		font-size: 15px;
		font-weight: 700;
		padding: 4px 12px;
		border: 1px solid #59cad7;
		border-radius: 5px;
		display: inline-block;
		vertical-align: middle;
		span {
			text-transform: none; } }
	.header_basket {
		margin-right: 13px; } }
