.footer {
	margin-top: 55px;
	.top_container {
		background: url('../img/footer_bg.png') no-repeat 50% 50%;
		background-size: cover;
		padding-bottom: 25px; }
	.container {
		text-align: center;
		position: relative;
		.footer_img {
			position: absolute;
			right: 285px;
			top: 10px; }
		.footer_bath_img {
			position: absolute;
			top: 45px;
			left: 250px;
			z-index: 0;
			height: 143px;
			overflow: hidden; }
		.footer_quastion {
			position: absolute;
			top: 3px;
			left: 435px; } }


	&_logo_wrapper {
		float: left;
		text-align: left;
		margin-top: 45px;
		.desing_block {
			font-size: 0;
			text-align: justify;
			padding-top: 20px;
			&:after {
				content: '';
				display: inline-block;
				width: 100%; }
			& > * {
				vertical-align: middle;
				display: inline-block; }
			a {
				color: $link_blue;
				font-size: 13px;
				font-weight: 700;
				text-align: right; } } }
	&_logo {
		display: inline-block;
		vertical-align: middle;
		padding-left: 8px;
		padding-right: 59px;
		padding-top: 13px;
		padding-bottom: 13px;
		// margin-right: 28px
		@include linear-gradient(to top, #7fe0f8 0%, #cef8ff 100%);
		position: relative;

		& > * {
			display: inline-block;
			vertical-align: middle; }
		&_shadow {
			position: absolute;
			top: 0;
			left: 0;
			display: block;
			width: 100%;
			height: 100%;
			&:before {
				content: url('../img/footer_left_shadow.png');
				position: absolute;
				top: 0;
				left: -35px; }
			&:after {
				content: url('../img/footer_right_shadow.png');
				position: absolute;
				top: 0;
				right: -35px; } }
		&_bottom {
			display: block;
			width: 100%;
			// height: 100%
			height: 42px;
			position: absolute;
			bottom: -40px;
			left: 0;
			background: #7fe0f8; }

		&_text {
			margin-left: -30px;
			font-family: 'Myriad Pro';
			font-style: italic;
			font-weight: bold;
			span {
				display: block;
				color: $blue;
				font-size: 36px;
				line-height: 38px;
				letter-spacing: -1.44px;
				&:last-child {
					font-size: 60px;
					letter-spacing: -2.4px; } } } }

	&_contacts {
		text-align: left;
		width: fit-content;
		display: inline-block;
		margin-top: 23px;
		margin-left: -120px;
		position: relative;
		z-index: 10;
		&:before {
			content: url('../img/footer_quastion.png');
			position: absolute;
			left: -100px;
			top: -20px; }

		&_h {
			color: $blue;
			font-size: 24px;
			text-transform: uppercase;
			font-weight: 700; }
		&_schedule {
			color: $blue;
			font-size: 18px;
			line-height: 17px;
			margin-top: 15px; }
		&_phone {
			display: block;
			font-size: 30px;
			font-weight: 700;
			margin-top: 8px;
			.pale_blue {
				color: #47949d; }
			.dark_blue {
				color: #20666e; } }
		&_mail {
			display: block;
			font-size: 21px;
			text-decoration: underline;
			line-height: 21px;
			color: $link_blue;
			&:hover {
				text-decoration: none; } }
		&_town {
			margin-top: 13px;
			color: $blue;
			font-size: 18px;
			font-weight: 700; } }

	&_social {
		float: right;
		margin-right: 75px;
		position: relative;
		&:before,
		&:after {
			content: '';
			position: absolute;
			top: 0;
			height: 95%;
			width: 2%;
			background: $white;
			z-index: 5; }
		&:before {
			left: 25px; }
		&:after {
			right: 25px; }
		.counter_block {
			height: 28px;
			text-align: center;
			max-width: 88px;
			margin: 10px auto;
			border: 1px solid; }

		.vk_link {
			margin-bottom: 15px; }
		a {
			display: block;
			position: relative;
			z-index: 10; } }


	.bottom_container {
		background: #92dcfc;
		color: $blue;
		font-size: 12px;
		text-align: center;
		padding: 13px;
		a {
			& > * {
				display: inline-block;
				vertical-align: middle; }
			span {
				margin-left: 5px;
				color: $link_blue;
				text-decoration: underline;
				font-weight: 700;
				font-size: 13px; } } } }
