.category {
	// margin-bottom: 55px
	h1 {
		text-align: center;
		font-size: 39px;
		color: $blue;
		margin-top: 15px;
		margin-bottom: 0px; }
	&_img {
		overflow: hidden;
		text-align: center;
		margin-bottom: 10px;
		position: relative;
		height: 47px;
		img {
			position: absolute;
			left: 50%;
			@include translate(-50%, 0); } }

	&_block {
		font-size: 0;
		margin-left: -10px;
		margin-right: -10px;
		text-align: center;
		&_link {
			@include cursor; }
		&_item {
			// border: 1px solid
			padding: 0 20px;
			margin-top: 28px;
			width: 15.24%;
			font-size: 20px;
			display: inline-block;
			vertical-align: top;
			margin-right: 10px;
			&:nth-child(6n) {
				margin-right: 0; }
			&_img {
				border: 1px solid $blue;
				height: 120px;
				margin-bottom: 13px;
				overflow: hidden;
				// display: table-cell
				vertical-align: middle;
				width: 100%;
				position: relative;
				@include transition(0.2s);
				&:hover {
					opacity: 0.5; }
				&_wrapper {
					position: relative;
					&:after {
						content: '';
						position: absolute;
						bottom: -5px;
						left: 0;
						width: 100%;
						height: 5px;
						background: $green; } }
				img {
					position: absolute;
					top: 50%;
					left: 50%;
					@include translate(-50%, -50%);
					// display: inline-block
					// vertical-align: middle
					max-width: 100%;
					// height: 100%
 } }					// object-fit: contain
			&_text {
				max-height: 50px;
				font-size: 18px;
				font-weight: 700;
				color: $blue;
				line-height: 25px;
				text-align: center;
				padding: 0 5px;
				margin-left: -22px;
				margin-right: -22px;
				&:hover {
					text-decoration: underline; } } } }
	&_p {
		color: $gray_text;
		font-size: 17px;
		line-height: 25px;
		letter-spacing: -0.46px;
		margin-top: 28px; }

	&_recently_viewed {
		&_block {
			margin-top: 30px;
			font-size: 0;
			text-align: justify;
			&:after {
				content: '';
				display: inline-block;
				width: 100%; }
			&_item {
				font-size: 14px;
				display: inline-block;
				vertical-align: top;
				width: 20%;
				text-align: center;
				margin-bottom: 35px;
				margin-right: 6.14%;
				&:nth-child(4n) {
					margin-right: 0; }
				&_h {
					display: block;
					height: 50px;
					text-align: center;
					color: $blue;
					font-size: 20px;
					line-height: 25px;
					font-weight: 700;
					margin-bottom: 7px;
					overflow: hidden;
					&:hover {
						text-decoration: underline; } }
				&_img {
					border: 1px solid $blue;
					overflow: hidden;
					height: 206px;
					position: relative;
					@include transition(0.2s);
					&:hover {
						opacity: 0.5; }
					img {
						max-width: 100%;
						max-height: 100%;
						position: absolute;
						top: 50%;
						left: 50%;
						@include translate(-50%, -50%); }
					&_wrapper {
						display: block;
						margin-bottom: 10px;
						position: relative;
						padding-bottom: 1px;
						border-bottom: 2px solid #fa0101;
						&:before,
						&:after {
							content: '';
							width: 2px;
							height: 99%;
							position: absolute;
							top: 0;
							background: #fa0101; }
						&:before {
							left: -3px; }
						&:after {
							right: -3px; } } }
				&_rating {
					font-size: 24px;
					color: #d3d3d3;
					height: 29px;
					& i.active {
						color: #fcd521; } }
				&_price {
					color: $white;
					position: relative;
					font-size: 30px;
					text-align: center;
					width: 90%;
					line-height: 33px;
					margin: auto;
					margin-top: 7px;
					& span {
						line-height: 26px; }
					& i {
						font-size: 14px; }
					& > div {
						position: relative;
						z-index: 10; }
					&:before {
						content: '';
						position: absolute;
						top: 0;
						left: 0;
						width: 100%;
						height: 100%;
						background: $blue;
						z-index: 5;
						@include transform(skew(-0.05turn, 0deg)); } }
				&_basket {
					@include cursor;
					border: 1px solid #59cad7;
					@include linear-gradient(to top, #cef8ff 0%, #7fe0f8 100%);
					color: #555555;
					font-size: 15px;
					font-weight: 700;
					text-shadow: {}
					display: block;
					margin: auto;
					text-align: center;
					max-width: 91px;
					padding: 3px 5px;
					margin-top: 10px;
					border-radius: 3px; } } } } }

.category_text_block {
	margin-top: 5px;
	&_p {
		color: $gray_text;
		font-size: 17px;
		line-height: 25px;
		letter-spacing: -0.46px;
		margin-top: 25px; } }

.category_baner {
	background: url('../img/banner_img.png') no-repeat 50% 50%;
	background-size: cover;
	margin-top: 38px;
	border-radius: 20px;
	padding-top: 45px;
	padding-bottom: 20px;
	padding-right: 15px;
	overflow: hidden;
	margin-bottom: 37px;
	&_caption {
		// border-left: 13px solid #da251c
		max-width: 700px;
		font-size: 33px;
		color: #393939;
		font-family: 'OpenSansExtra';
		line-height: 43px;
		text-shadow: 1px 1px 0 rgba(255, 255, 255, 0.53);
		text-transform: uppercase;
		position: relative;
		z-index: 10;
		padding: 20px 17px;
		padding-left: 30px;
		span {
			position: relative;
			z-index: 10; }
		&:before {
			content: '';
			height: 100%;
			width: 13px;
			background: #da251c;
			position: absolute;
			top: 0;
			left: 0;
			z-index: 10; }
		&:after {
			content: '';
			position: absolute;
			top: 0;
			right: 0;
			background: $white;
			opacity: 0.6;
			width: 130%;
			height: 100%;
			z-index: 5;
			@include transform(skew(-0.06turn, 0deg)); } }
	&_p {
		font-family: 'OpenSans';
		font-size: 26px;
		line-height: 41px;
		text-transform: uppercase;
		color: #393939;
		margin-top: 7px;
		padding-left: 40px; } }


.category_footer {
	margin-top: 10px; }
