* {
	box-sizing: border-box; }
html {
	width: 100%;
	height: 100%; }
body {
	margin: 0;
	padding: 0;
	font-family: Calibri;
	width: 100%;
	min-width: 320px; }
.container {
	max-width: ($max_width + $content_padd_left + $content_padd_right);
	padding-left: $content_padd_left;
	padding-right: $content_padd_right;
	// width: 100%
	margin: auto;
	height: 100%; }
img {
	border: none; }
a {
	text-decoration: none;
	color: #000;
	outline: none; }
p,h1,h2,h3,h4,h5 {
	margin: 0; }
.slick-slide {
	outline: none; }

.main_container {
	font-size: 0;
	& > * {
		display: inline-block;
		vertical-align: top; }
	aside {
		width: 23.58%;
		font-size: 14px;
		margin-right: 1.95%; }
	main {
		width: 74.47%;
		font-size: 14px; } }

.slick-arrow {
	padding: 0; }

.page_small_h {
	display: block;
	text-align: center;
	padding: 0 10px;
	font-size: 30px;
	font-weight: 700;
	letter-spacing: -0.6px;
	color: $blue;
	&_img {
		text-align: center;
		padding: 0 10px;
		overflow: hidden;
		position: relative;
		height: 28px;
		img {
			position: absolute;
			left: 50%;
			@include translate(-50%, 0); } } }


.grad_btn {
	@include linear-gradient(to top, #cef8ff 0%, #7fe0f8 100%);
	position: relative;
	span {
		position: relative;
		z-index: 10;
		text-transform: uppercase; }
	&:after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 5;
		@include linear-gradient(to top, #22AAC8 0%, #CCF7FE 100%);
		opacity: 0;
		@include transition(0.2s); }
	&:hover {
		&:after {
			opacity: 1; } } }
