.item_content {
	&_h {
		text-align: center;
		font-size: 39px;
		color: $blue;
		margin-top: 35px;
		margin-bottom: 0px; }
	&_img {
		overflow: hidden;
		text-align: center;
		margin-bottom: 25px;
		position: relative;
		height: 47px;
		img {
			position: absolute;
			left: 50%;
			@include translate(-50%, 0); } }
	&_container {
		font-size: 0; }
	&_slider {
		font-size: 14px;
		display: inline-block;
		vertical-align: top;
		width: 43.14%;
		.slider-for {
			width: 100%;
			height: 325px;
			.slick-list,
			.slick-track {
				width: 100%;
				height: 100%; }
			.item {
				width: 100%;
				height: 100%;
				overflow: hidden;
				position: relative;
				@include cursor;
				&:after {
					content: url('../img/zoom_icon.png');
					position: absolute;
					top: 5px;
					right: 5px; }
				img {
					width: 100%;
					height: 100%;
					object-fit: cover; } } }
		.slider-nav {
			margin-left: -10px;
			margin-right: -9px;
			margin-top: 10px;
			background: #f3fcff;
			padding-top: 5px;
			padding-bottom: 45px;
			position: relative;
			&:before,
			&:after {
				content: '';
				width: 10px;
				height: 100%;
				position: absolute;
				top: 0;
				background: $white; }
			&:before {
				left: 0; }
			&:after {
				right: 0; }
			.slick-list {
				padding-bottom: 6px; }
			.item {
				width: 103px;
				max-height: 103px;
				// overflow: hidden
				// margin-left: 18px
				padding-left: 10px;
				padding-right: 10px;
				@include cursor;
				&_wrapper {
					position: relative;
					border: 1px solid transparent;
					@include transition(0.2s);
					&:hover {
						border: 1px solid $blue;
						&:after {
							opacity: 1; } }
					&:after {
						content: '';
						width: 100%;
						height: 5px;
						background: $green;
						border-left: 1px solid $green;
						border-right: 1px solid $green;
						position: absolute;
						bottom: -6px;
						left: -1px;
						@include transition(0.2s);
						opacity: 0; }
					&_img {
						overflow: hidden;
						img {
							width: 100%;
							height: 100%;
							object-fit: cover; } } } }

			.slick-arrow {
				background: none;
				border: none;
				font-size: 0;
				position: absolute;
				// top: 123px
				bottom: 5px;
				// @include translate(0, -50%)
				z-index: 10;
				outline: none;
				@include cursor;
				@include transition(0.2s);
				&:hover {
					opacity: 0.7; }
				// &:hover
				// 	@include transform(translate(0, -50%) rotate(180deg))
				// 	&:after
				// 		content: url('../img/arrow_hover.png')

				&:after {
					content: url('../img/hit_slider_arrow.png'); }
				&.slick-prev {
					left: 10px; }
				&.slick-next {
					@include transform(rotate(180deg));
					right: 10px; } } } }

	&_block {
		width: 54.29%;
		display: inline-block;
		vertical-align: top;
		margin-left: 2.57%;
		// font-size: 14px
		background: #f3fcff;
		padding: 30px 15px 10px 10px;
		&_price {
			display: inline-block;
			vertical-align: top;
			font-size: 14px;
			width: 60%;
			.item_price {
				font-size: 55px;
				line-height: 53px;
				color: $white;
				background: $blue;
				text-align: center;
				margin-bottom: 18px;
				position: relative;
				z-index: 10;
				&:after {
					content: '';
					position: absolute;
					top: 0;
					right: -10px;
					background: $blue;
					width: 100%;
					height: 100%;
					z-index: -1;
					@include transform(skew(-0.04turn, 0deg)); }

				span {
					i {
						font-size: 26px; } } }
			.item_counter {
				position: relative;
				width: 127px;
				margin-left: auto;
				text-align: center;
				input {
					text-align: center;
					border: 2px solid #3c555b;
					background: $white;
					border-radius: 10px;
					font-size: 18px;
					width: 53px;
					height: 32px; }
				.count_btn {
					span {
						position: absolute;
						top: -1px;
						background: $blue;
						border-radius: 5px;
						width: 34px;
						height: 34px;
						line-height: 34px;
						font-size: 18px;
						color: $white;
						text-align: center;
						@include cursor;
						padding-top: 1px;
						@include transition(0.2s);
						&:hover {
							opacity: 0.7; } }
					.plus {
						right: 0px; }
					.minus {
						left: 0px; } } } }

		&_btn {
			display: inline-block;
			vertical-align: top;
			font-size: 14px;
			width: 40%;

			&_basket {
				color: #555555;
				font-size: 30px;
				font-weight: 700;
				display: block;
				margin-left: auto;
				text-align: center;
				max-width: 185px;
				padding: 7px 5px;
				// margin-top: 10px
				border-radius: 3px;
				border: 1px solid #59cad7;
				@include cursor;
				@include linear-gradient(to top, #cef8ff 0%, #7fe0f8 100%); }
			&_buy {
				display: block;
				border: 1px solid #4dab1e;
				background: $green;
				font-size: 15px;
				color: $white;
				text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.29);
				font-weight: 700;
				text-align: center;
				padding: 10px 5px;
				max-width: 185px;
				margin-left: auto;
				margin-top: 10px;
				@include transition(0.2s);
				&:hover {
					background: darken($green, 5%); } } } } }

.item_tab {
	font-size: 14px;
	margin-top: 40px;
	&_list {
		list-style-type: none;
		padding: 0;
		margin: 0;
		font-size: 0; }
	&_item {
		display: inline-block;
		color: #555555;
		font-size: 18px;
		font-weight: 700;
		text-transform: uppercase;
		@include linear-gradient(to top, #cef8ff 0%, #7fe0f8 100%);
		border: 1px solid #59cad7;
		border-radius: 10px 10px 0 0;
		padding: 8px 20px;
		@include cursor;
		margin-right: 2px;
		&.active {
			border: 1px solid #ffffff;
			background: #f5f5f5;
			@include box-shadow(inset 1px 1px 1px rgba(0, 0, 0, 0.13)); } } }

.item_tab {
	&_content {
		&_block {
			display: none;
			&.active {
				display: block; }

			background: $white;
			padding: 12px;
			font-size: 17px;
			&#description {
				padding-top: 23px; }
			.description_characteristic {
				padding: 0;
				margin: 0;
				margin-top: 30px;
				list-style-type: none; }
			.description_offer {
				&_h {
					font-weight: 700;
					margin-top: 30px; }
				&_list {
					// list-style-image:
					list-style-type: none;
					margin: 0;
					padding: 0;
					margin-top: 30px;
					li {
						padding-left: 23px;
						background: url('../img/list_style_img.png') no-repeat 0px 3px;
						a {
							text-decoration: underline;
							color: $link_blue;
							&:hover {
								text-decoration: none; } } } } }
			&#delivery {
				padding-left: 4px;
				padding-right: 2px;
				padding-top: 1px;
				padding-bottom: 0; }
			& .delivery {
				&_h {
					color: #47949d;
					font-size: 14px;
					font-weight: 700;
					padding-left: 33px;
					background: url('../img/list_style_img.png') no-repeat 0px 1px;
					line-height: 18px;
					margin-top: 12px;
					margin-bottom: 12px; }
				&_table {
					display: table;
					position: relative;
					&:before {
						content: '';
						width: 5px;
						height: 5px;
						background: $blue;
						position: absolute;
						bottom: -2px;
						left: -3px;
						border-radius: 50%;
						z-index: 10; }
					&_row {
						display: table-row;
						&:first-child {
							.delivery_table_cell {
								padding-top: 15px;
								border-top: 1px solid #9fcbd0;
								&:before {
									content: '';
									width: 5px;
									height: 5px;
									background: $blue;
									position: absolute;
									top: -3px;
									right: -3px;
									border-radius: 50%;
									z-index: 10; }
								&:first-child {
									font-size: 14px; } } } }
					&_cell {
						border-right: 1px solid #9fcbd0;
						border-bottom: 1px solid #9fcbd0;
						display: table-cell;
						font-weight: 700;
						font-size: 13px;
						color: $gray_text;
						text-align: center;
						letter-spacing: -0.35px;
						padding: 10px 2px;
						position: relative;
						&:first-child {
							max-width: 123px;
							&:after {
								content: '';
								width: 5px;
								height: 5px;
								background: $blue;
								position: absolute;
								top: -3px;
								left: -3px;
								border-radius: 50%;
								z-index: 10; } }
						&:nth-child(2) {
 }							// max-width: 62px
						&:nth-child(3) {
 }							// max-width: 93px
						.fa {
							font-size: 12px; } } }

				&_stock {
					background: #b3f0fd;
					padding: 14px 10px;
					font-size: 14px;
					font-weight: 700;
					position: relative;
					margin-top: 1px;
					&:before {
						content: '';
						width: 100%;
						height: 1px;
						background: #9fcbd0;
						position: absolute;
						bottom: -2px;
						left: 0; }
					&:after {
						content: '';
						width: 5px;
						height: 5px;
						background: $blue;
						position: absolute;
						bottom: -4px;
						left: -3px;
						border-radius: 50%; }
					&_h {
						display: table-cell;
						min-width: 130px;
						padding-left: 25px;
						background: url('../img/list_style_img.png') no-repeat 0px 1px;
						.fa {
							font-size: 12px; } }
					&_text {
						display: table-cell; } }
				&_table_second {
					font-size: 0;
					border-bottom: 1px solid #9fcbd0;
					position: relative;
					&:before {
						content: '';
						width: 5px;
						height: 5px;
						background: $blue;
						position: absolute;
						top: -2px;
						left: -3px;
						border-radius: 50%;
						z-index: 10; }
					&:after {
						content: '';
						width: 5px;
						height: 5px;
						background: $blue;
						position: absolute;
						top: -2px;
						left: 50%;
						@include translate(-3px, 0);
						border-radius: 50%;
						z-index: 10; }
					&_name,
					&_price {
						font-size: 14px;
						width: 50%;
						font-weight: 700;
						display: inline-block;
						vertical-align: top;
						div {
							padding: 13px 5px;
							border-top: 1px solid #9fcbd0;
							border-right: 1px solid #9fcbd0; } }
					&_name {
						div {
							position: relative;
							&:before {
								content: '';
								width: 5px;
								height: 5px;
								background: $blue;
								position: absolute;
								bottom: -3px;
								left: -3px;
								border-radius: 50%;
								z-index: 10; } } }
					&_price {
						text-align: center; } }

				&_info {
					margin-top: 1px;
					&_h {
						background: $green url('../img/delivery_info_icon.png') no-repeat 13px 10px;
						color: $black;
						font-size: 14px;
						font-weight: 700;
						padding: 10px;
						padding-left: 43px; }
					&_list {
						list-style-type: none;
						padding: 0;
						border-bottom: 1px solid #dadada;
						&_item {
							padding: 13px;
							padding-left: 44px;
							border-top: 1px solid #9fcbd0;
							background: url('../img/dot_icon.png') no-repeat 13px 20px;
							font-size: 14px;
							font-weight: 700;
							color: $gray_text;
							position: relative;
							.fa {
								font-size: 13px; }
							&:before {
								content: '';
								width: 5px;
								height: 5px;
								background: $blue;
								position: absolute;
								top: -3px;
								left: -3px;
								border-radius: 50%;
								z-index: 10; } } } } } } } }

.reviews {
	&_h {
		color: $blue;
		font-weight: 700;
		font-size: 18px;
		text-transform: uppercase;
		border-bottom: 1px solid #d8d8d8;
		margin-top: 15px; }
	form {
		font-size: 0;
		padding-top: 12px;
		position: relative;
		margin-bottom: 50px;
		div {
			font-size: 14px;
			// display: inline-block
			// vertical-align: top
			position: relative;
			&:first-child {
				position: absolute; }
			&:nth-child(2) {
				margin-left: 283px;
				display: block; }
			input {
				height: 37px;
				border-radius: 10px;
				border: 1px solid #3c555b;
				padding: 5px;
				outline: none; }
			.code_img {
				position: absolute;
				bottom: -2px;
				left: 0; }
			.rewies_name {
				display: block;
				margin-bottom: 13px;
				width: 100%;
				margin-top: 10px; }
			.reviews_code {
				display: block;
				margin-left: 100px;
				width: 63%; }
			textarea {
				resize: none;
				width: 100%;
				height: 110px;
				border-radius: 10px;
				border: 1px solid #3c555b;
				padding: 5px;
				outline: none; }
			&:nth-child(1) {
				width: 273px;
				margin-right: 10px; }
			&:nth-child(2) {
 } }				// width: 71.8%
		button {
			@include linear-gradient(to top, #cef8ff 0%, #7fe0f8 100%);
			width: 181px;
			height: 26px;
			border: 1px solid #59cad7;
			border-radius: 5px;
			color: #555555;
			font-size: 15px;
			font-weight: 700;
			margin-top: 15px;
			margin-left: 50px;
			@include cursor; } }
	&_block {
		margin-top: 10px;
		background: #f3fcff;
		padding: 14px 10px 26px;
		&_name {
			color: #555555;
			font-size: 17px;
			font-weight: 700; }
		&_time {
			font-size: 15px;
			color: #acacac;
			font-weight: 300; }
		&_text {
			font-size: 17px;
			color: $gray_text;
			margin-top: 20px; } } }

.item_page {
	padding-top: 1px; }
.related_products,
.recommended_products {
	margin-top: 53px;
	&_h {
		display: block;
		text-align: center;
		padding: 0 10px;
		font-size: 30px;
		font-weight: 700;
		letter-spacing: -0.6px;
		color: $blue; }
	&_img {
		text-align: center;
		padding: 0 10px;
		overflow: hidden;
		position: relative;
		height: 28px;
		img {
			position: absolute;
			left: 50%;
			@include translate(-50%, 0); } }

	&_block {
		margin-top: 30px;
		font-size: 0;
		text-align: justify;
		&:after {
			content: '';
			display: inline-block;
			width: 100%; }
		&_item {
			font-size: 14px;
			display: inline-block;
			vertical-align: top;
			width: 20%;
			text-align: center;
			&_h {
				display: block;
				height: 50px;
				text-align: center;
				color: $blue;
				font-size: 20px;
				line-height: 25px;
				font-weight: 700;
				margin-bottom: 7px;
				overflow: hidden;
				&:hover {
					text-decoration: underline; } }
			&_img {
				border: 1px solid $blue;
				overflow: hidden;
				height: 206px;
				position: relative;
				@include transition(0.2s);
				&:hover {
					opacity: 0.5; }
				img {
					max-width: 100%;
					max-height: 100%;
					position: absolute;
					top: 50%;
					left: 50%;
					@include translate(-50%, -50%); }
				&_wrapper {
					display: block;
					margin-bottom: 10px;
					position: relative;
					padding-bottom: 1px;
					border-bottom: 2px solid #fa0101;
					&:before,
					&:after {
						content: '';
						width: 2px;
						height: 99%;
						position: absolute;
						top: 0;
						background: #fa0101; }
					&:before {
						left: -3px; }
					&:after {
						right: -3px; } } }
			&_rating {
				font-size: 24px;
				color: #d3d3d3;
				height: 29px;
				& i.active {
					color: #fcd521; } }
			&_price {
				color: $white;
				position: relative;
				font-size: 30px;
				text-align: center;
				width: 90%;
				line-height: 33px;
				margin: auto;
				margin-top: 7px;
				& span {
					line-height: 26px; }
				& i {
					font-size: 14px; }
				& > div {
					position: relative;
					z-index: 10; }
				&:before {
					content: '';
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					background: $blue;
					z-index: 5;
					@include transform(skew(-0.05turn, 0deg)); } }
			&_basket {
				@include cursor;
				border: 1px solid #59cad7;
				@include linear-gradient(to top, #cef8ff 0%, #7fe0f8 100%);
				color: #555555;
				font-size: 15px;
				font-weight: 700;
				text-shadow: {}
				display: block;
				margin: auto;
				text-align: center;
				max-width: 91px;
				padding: 3px 5px;
				margin-top: 10px;
				border-radius: 3px; } } } }
